import React, { Component } from 'react';
import './TimeApp.css';

class TimeApp extends Component {
  constructor(props) {
  super(props);
  this.state = { tasks: [
          {desc: '', starttime: '', endtime: '', date: '' }]};
  }
  render() {
    return (
      <div className="TimeApp">
        Date: {this.props.date}, Start Time: {this.props.starttime}, End Time: {this.props.endtime}, Description:{this.props.desc}
        <br/>
      </div>
    );
  }
}

export default TimeApp;


