import React from 'react';
import './App.css';
import TimeList from './TimeList';
import logo from './logo.png';

const App = (props) => {
  return (
	    <div className="App">
	        <div className="App-header">
	           	<div className="container">
          			<img src={logo} className="App-logo" alt="logo" />
        		</div>
        	</div>
        	<div className="container">
	      		<TimeList name={props.params.name}/>
	    	</div>
    	</div>
  );
};

export default App;


