import React, { Component } from 'react';
import TimeApp from './TimeApp';
import AddTime from './AddTime';
import './TimeList.css';

class TimeList extends Component {
	constructor(props) {
  		super(props);
  		this.state = { tasks: [], timecounter: 0};
  		this.addTimeItem = this.addTimeItem.bind(this);
  		this.addCount = this.addCount.bind(this);
	}
	addTimeItem(task) {
		console.log(JSON.stringify(task));
  		var tasks = this.state.tasks.concat(task);
   		this.setState({tasks: tasks});
	}
	addCount(moretime) {
		var timer = this.state.timecounter + moretime;
		this.setState({timecounter: timer});
	}
 	render() {
 		let d = new Date();
		let	ct = d.toDateString() + " " + d.getHours() + ":" + d.getMinutes();
		var name = this.props.name.split("-");
		let FullName = name[0] + " " + name[1];
 		return (
      <div className="TimeList">
      	<div className="AppHeader">
      		<div className="HeaderItem">
      			Current Date / Time: {ct}
      		</div>
      		<div className="HeaderItem">
      			Total Time: {this.state.timecounter}
      		</div>
      		<div className="HeaderItem">
      			Your Name: {FullName}
      		</div>
      	</div>
      	<AddTime addTimeItem={this.addTimeItem} addCount={this.addCount} />
        {this.state.tasks.map(function(task, index) {
                    return <TimeApp
                                key={index}
                                desc={task.desc}
                                date={task.date}
                                starttime={task.starttime}
                                endtime={task.endtime}/>
                })}
      </div>
    );
  }
}
export default TimeList;


