import React, { Component } from 'react';
import './Welcome.css';
import Button from 'react-bootstrap/lib/Button';
import logo from './logo.png';
import { browserHistory } from 'react-router';

class Welcome extends Component {
  constructor(props) {
  super(props);
  this.validate = this.validate.bind(this);
  }
  validate() {
    if(this.refs.fName.value && this.refs.lName.value && this.refs.email.value) {
      var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (re.test(this.refs.email.value)) {
        var newURL = '/time-log/' + this.refs.fName.value + "-" + this.refs.lName.value;
        browserHistory.push(newURL);
      }
      else {
        alert("Email Address Invalid");
      }
    }
    else {
      alert("All fields need to be submitted");
    }
  }
  render() {
    return (
      <div className="Welcome">
        <div className="App-header">
              <div className="container">
                <img src={logo} className="App-logo" alt="logo" />
            </div>
          </div>
          <div className="container">
            <div className="Login">
            <h1>Welcome</h1>
            <p>Please Login Below</p><br/>
              <input type="text" ref="fName" placeholder="First Name" />&nbsp;
              <input type="text"ref="lName" placeholder="Last Name" />&nbsp;
              <input type="email" ref="email" placeholder="Email" />&nbsp;
              <Button bsStyle="success" onClick={this.validate}>Log In</Button>
            </div>
          </div>
      </div>
    );
  }
}

export default Welcome;


