import React, { Component } from 'react';
import Button from 'react-bootstrap/lib/Button';
import './AddTime.css';

class AddTime extends Component {
  constructor(props) {
    super(props);
    this.state = { tasks: [
      {desc: '', starttime: '', endtime: '', date: '' }]};
    this.addTimeItem = this.addTimeItem.bind(this);
  }
  addTimeItem() {
    var tasks = this.state.tasks[0];
    tasks.date = this.refs.date.value;
    tasks.starttime = this.refs.st.value;
    tasks.endtime = this.refs.et.value;
    tasks.desc = this.refs.desc.value;
    if (tasks.date && tasks.starttime && tasks.endtime && tasks.desc) {
      var starttime = tasks.starttime.split(":");
      var endtime = tasks.endtime.split(":");;
      if (endtime[0] > starttime[0] || (starttime[0] === endtime[0] && starttime[1] < endtime[1]))
      {
        var totaltime = endtime[0] - starttime[0] + ((endtime[1] - starttime[1]) / 60 );
        this.props.addCount(totaltime);
      }
      this.setState({tasks:tasks});
      this.props.addTimeItem(this.state.tasks);
      this.setState({ tasks: [
            {desc: '', starttime: '', endtime: '', date: '' }]});
      this.refs.date.value = "";
      this.refs.st.value = "";
      this.refs.et.value = "";
      this.refs.desc.value = "";
    }
  }

  render() {
    return (

      <div className="AddTime">
      <input type="text" className="date"  ref="date" placeholder="Date" />&nbsp;
      <input type="text" className="time" ref="st" placeholder="Start Time (HH:MM)" />&nbsp;
      <input type="text"  className="time" ref="et" placeholder="End Time (HH:MM)" />&nbsp;
      <input type="text" ref="desc" placeholder="Description" />&nbsp;
      Add Time Item <Button bsStyle="default" onClick={this.addTimeItem}>+</Button>
    </div>
    );
  }
}

export default AddTime;


